/* #region page */
.page {
  font-family: poppins, sans-serif;
  color: #ffffff;
  line-height: 140%;
  font-size: 16px;
  background-color: #1d1d1d;
  scroll-behavior: smooth;

  &__corpo {
    margin: 0;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    transform: translateX(-100%);

    &:target {
      opacity: 1;
      pointer-events: all;
      transform: translateX(0);
    }
  }
}

/* #endregion */
